import React, {useState} from 'react';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import SwipeableViews from 'react-swipeable-views';
import Work from './pages/Work';
import Music from './pages/Music';
import '../styles/select.css';


const styles = {
  tabs: {
    background: '#fff',
  },
  slide: {
    padding: 15,
    minHeight: 450,
    color: '#fff',
  },
  slide1: {
    backgroundColor: '#FEA900',
  },
  slide2: {
    backgroundColor: '#B3DC4A',
  },
  slide3: {
    backgroundColor: '#6AC0FF',
  },
};


const Select = () => {
  const [swipeableActions, setSwipeableActions] = useState('');
  const [tabIndex, setTabIndex] = useState(1);

  const handleChange = (event, value) => {
      setTabIndex(value);
  };

    return (
      <div className="selectContent">
        <Tabs 
          value={tabIndex} 
          onChange={handleChange} 
          style={styles.tabs}
          variant="fullWidth"
        >
          <Tab label="history" /> 
          <Tab label="Work" />
          <Tab label="Music" />
        </Tabs>
          <SwipeableViews 
            animateHeight
            resistance
            index={tabIndex}
            onChangeIndex={handleChange}
          >
            <div style={Object.assign({}, styles.slide, styles.slide1)}><Music /></div>
            <div style={Object.assign({}, styles.slide, styles.slide2)}><Work /></div>
            <div style={Object.assign({}, styles.slide, styles.slide3)}><Music /></div>
          </SwipeableViews>
      </div>
    );
  };

export default Select;